.card {
  /* border-radius: 2px; */
  height: 100px;
  width: 100px;;
  margin: 1rem;
  position: relative;
  -webkit-transition: all .2s ease-in-out;
  transition: all .2s ease-in-out;
}

.card > .img-container {
  height: 100px;
  width: 179px;
  background:rgb(255, 255, 255);
}

.card > .img-container > img {
  height: 100%;
}


nav {
  width: 100%;
  font-size: 3rem;
  color: white;
  margin-bottom: 50px;
  background-color: #003E7E;
  height: auto;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  color: white;
}

a:hover {
  color: darkgrey;
  text-decoration: none;
}

nav li {
  display: inline-block;
  padding: 26px 0;
  width: 25%;
  /* text-align: center; */
}

ul {
  /* padding-left: 0px; */
  display: flex;
  align-items: center;
}

@media only screen and (max-width: 750px) {
  #cur-sco, #top-sco {
    font-size: 2rem;
  }
}
.wrapper {
  background: rgb(255, 255, 255);
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
  padding-bottom: 50px;
}
.title {
  width: 100%;
  text-align: right;
  font-size: 2rem;
  color: #444;
  margin-bottom: 50px;
  padding-left: 3%;
  padding-right: 3%;  
}

.title {
  width: 100%;
  text-align: center;
  margin-bottom: 50px;
}
* {
  box-sizing: border-box;
  font-family: 'Staatliches', cursive;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}
